$marginX: .6rem;
@mixin hide-on-mobile {
	@media screen and (max-width: 420px) {
		display: none
	}
}



@mixin align-head {
	flex-basis: 25%;
	padding: 1rem $marginX;
}

@mixin align-list {
	flex-basis: 25%;
	padding: 1rem $marginX;
	text-align: left;
}

.contacts {
	padding-left: 15%;
	padding-right: 15%;
	height: 90vh;
	overflow-y: scroll;
	overflow-x: hidden;
	@media screen and (max-width: 420px) {
		padding-left: 0;
		padding-right: 0;
		height: calc(80.5vh - 4px);
	}
	&__heading {
		border-bottom: 1px solid silver;
		position: sticky;
		top: 0;
		background: white;
	}

	&__counter {
    padding: 1rem $marginX .3rem $marginX;
  }

	&__heading {
		display: flex;
		justify-content: space-between;
		font-weight: bold;
		@include hide-on-mobile;
	}

	&__heading-item {
		@include align-head;

	}

	&__counter {
	  // width: calc(100% - 16px);
	  color: silver;
	  font-weight: 500;
	}

	&__item {
		display: flex;
		justify-content: space-between;
		cursor: pointer;
	}

	&__name {
		@include align-head;
		@media screen and (max-width: 420px) {
			flex-basis: calc(100% - 16px);
		}
	}

	&__list-item:not(.contacts__name) {
		@include align-head;
		@include hide-on-mobile;
	}

}

$marginX: .5rem;
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  font-size: 14px;
  overflow-y: hidden;
  @media screen and (max-width: 420px) {
    margin: 8px;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  height: 10vh;
  padding-left: 15%;
	padding-right: 15%;
  @media screen and (max-width: 420px) {
    flex-direction: column;
    align-items: initial;
    height: calc(19.5vh - 4px);
    padding-left: 0;
		padding-right: 0;
  }

  &__logo {
    display: flex;
    @media screen and (max-width: 420px) {
      width: 100%;
    }
  }

  &__image {
    padding-left: $marginX;

  }

	&__title {
    font-weight: 500;
    padding-left: 1rem;
    margin-right: auto;
    @media screen and (max-width: 420px) {
      font-size: 25px;
      margin: .5em 0;
    }
	}
}

.header__search {
  border: none;
  margin-right: 25%;
  background: #f1f3f4;
  font-size: 1em;
  padding: 1em 1.9em;
  border-radius: 2px;
  width: 50%;
  @media screen and (max-width: 420px) {
    margin-right: 0;
    background: white;
    border-bottom: 1px solid silver;
    width: auto;
    padding: 1em $marginX;
    width: calc(100% - 16px);
  }
}

$accent-color: #1A73E8;
.popup-edit {
  display: none;
  position: absolute;
  // height: 20vh;
  width: 40vw;
  top: 40vh;
  left: 30vw;
  background: white;
  border-radius: 10px;
  box-shadow: 0 12px 15px 0 rgba(0,0,0,0.24);
  @media screen and (max-width: 420px) {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    border-radius: 0;
    box-shadow: none;
  }

	&__header {
    // padding: 12px 24px 10px 24px;
    padding: 24px;
    border-bottom: 1px solid silver;
    display: flex;
    align-items: baseline;

	}

	&__title {
    font-weight: 500;
    font-size: 1.2rem;
    margin: 0;
	}

  &__hide {
    margin-left: auto;
    cursor: pointer;
  }

  &__form {
    padding: 10px 24px 20px 24px;
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid silver;
	}

  &__input {
    width: 100%;
    margin-bottom: 1.5em;
    display: flex;
    align-items: center;
    &:nth-child(1) {
      margin-bottom: 1em;
    }

    & img[alt="user"] {
      margin-right: 24px;
    }

    & img:not([alt="user"]) {
      margin-left: 1em;
      margin-right: 2.5em;
    }
    & input {
      width: 70%;
      height: 24px;
      border: none;
      border-bottom: 2px solid silver;
    }

    & input:focus {
      outline: none;
      border-bottom: 2px solid $accent-color;
    }
	}

	&__name {

	}

	&__email {

	}

	&__phone {

	}

	&__company {

	}

	&__footer {
    padding: 10px 24px 10px 24px;
    display: flex;
    @media screen and (max-width: 420px) {
      position: absolute;
      bottom: 0;
      width: calc(92% - 16px);
    }
	}

  &__button {
    border: none;
    color: $accent-color;
    background: white;
    font-size: 1.1rem;
    cursor: pointer;
  }

	&__cancel {
    margin-left: auto;
	}

	&__save {
    margin-left: 40px;
	}

}

$accent-color: #1A73E8;

.popup-overlay {
  position: absolute;
  top: 0;
  left: 0;
  background: black;
  opacity: 0.5;
  width: 100vw;
  height: 100vh;
  display: none;
}

.popup-show {
  display: none;
  position: absolute;
  // height: 20vh;
  width: 40vw;
  top: 40vh;
  left: 30vw;
  background: white;
  border-radius: 10px;
  box-shadow: 0 12px 15px 0 rgba(0,0,0,0.24);
  @media screen and (max-width: 420px) {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    border-radius: 0;
    box-shadow: none;
  }

  &__header {
    padding: 24px;
    border-bottom: 1px solid silver;
    display: flex;
    align-items: baseline;
  }

  &__name {
    font-weight: 500;
    font-size: 1.2rem;
    margin: 0;
  }

  &__edit {
    margin-left: auto;
    cursor: pointer;
  }

  &__hide {
    margin-left: 2rem;
    cursor: pointer;
  }

  &__body {
    padding: 25px;
  }

  &__details {
    font-size: 1.2em;
  }

  &__info {
    margin: 1.2em 0;

    & a {
      color: $accent-color;
      text-decoration: none;
    }
  }

  &__image {
    margin-left: 1.5em;
    margin-right: 2em;
  }

}
